import { Component, OnInit, Renderer2, OnDestroy } from '@angular/core';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { NgbAccordionConfig } from '@ng-bootstrap/ng-bootstrap';
import * as Rellax from 'rellax';
import {ServiceService} from '../service.service';
import {NotificationComponent} from './notification/notification.component';
import * as _ from 'lodash';

@Component({
    selector: 'app-components',
    templateUrl: './components.component.html',
    styles: [`
    ngb-progressbar {
        margin-top: 5rem;
    }
    `]
})

export class ComponentsComponent implements OnInit, OnDestroy {
    data: Date = new Date();

    page = 4;
    page1 = 5;
    page2 = 3;
    focus;
    focus1;
    focus2;

    date: {year: number, month: number};
    model: NgbDateStruct;

    public isCollapsed = true;
    public isCollapsed1 = true;
    public isCollapsed2 = true;

    state_icon_primary = true;

    public rows: any[] = [];
    public rows2: any[] = [];

    public obj: any = {
        username: '',
        fullname: '',
        email: '',
        psw: '',
        inviterId: null,
    };
    public obj_a: any = {
        username: '',
        fullname: '',
        email: '',
        psw: '',
        inviterId: null,
    };
    public error = false;
    public success = false;
    public notificationId: number;

    constructor(
        private renderer: Renderer2, config: NgbAccordionConfig,
        public srv: ServiceService) {
        config.closeOthers = true;
        config.type = 'info';
    }
    isWeekend(date: NgbDateStruct) {
        const d = new Date(date.year, date.month - 1, date.day);
        return d.getDay() === 0 || d.getDay() === 6;
    }

    isDisabled(date: NgbDateStruct, current: {month: number}) {
        return date.month !== current.month;
    }

    ngOnInit() {
        this.onLoadData();
        // this.onLoadData2();
      const rellaxHeader = new Rellax('.rellax-header');

        const navbar = document.getElementsByTagName('nav')[0];
        navbar.classList.add('navbar-transparent');
        const body = document.getElementsByTagName('body')[0];
        body.classList.add('index-page');
    }
    ngOnDestroy() {
        const navbar = document.getElementsByTagName('nav')[0];
        navbar.classList.remove('navbar-transparent');
        const body = document.getElementsByTagName('body')[0];
        body.classList.remove('index-page');
    }
    /*=================== FROM HERE ==============================*/
    public checkObj(): boolean {
        if (!this.obj.username || !this.obj.fullname || !this.obj.email
            || !this.obj.psw || !this.obj.inviterId) {
            return false
        } else { return true; }
    }

    public onDelete(username: string): void {
        this.srv.deleteUser(username).subscribe((data: any) => {
            this.onLoadData();
            setTimeout (() => {
                this.onLoadData2();
            }, 100);
        }, (e) => {
            console.log('ERROR:', e);
        });
    }
    public onDelete2(username: string): void {
        this.srv.deleteUser2(username).subscribe((data: any) => {
            this.onLoadData2();
            setTimeout (() => {
                this.onLoadData();
            }, 100);

        }, (e) => {
            console.log('ERROR:', e);
        });
    }

    public onAdd(): void {
        // this.srv.createNewUser(this.obj).subscribe((result: any) => {
        // tslint:disable-next-line:max-line-length
        this.srv.createNewUser(this.obj.username, this.obj.fullname, this.obj.email, this.obj.psw, this.obj.inviterId).subscribe((result: any) => {
            if (result.message = 'Added') {
                this.obj = _.cloneDeep(this.obj_a);
                this.onLoadData();
                setTimeout (() => {
                    this.onLoadData2();
                }, 100);

            }}, (e) => {
            console.log('ERROR:', e);
        });
    }
    public onAdd2(): void {
        // this.srv.createNewUser(this.obj).subscribe((result: any) => {
        this.srv.createNewUser2(this.obj.username, this.obj.fullname, this.obj.email, this.obj.psw, this.obj.inviterId).subscribe((result: any) => {
            if (result.message = 'Added') {
                this.obj = _.cloneDeep(this.obj_a);
                setTimeout (() => {
                    this.onLoadData();
                }, 100);
                this.onLoadData2();
            }}, (e) => {
            console.log('ERROR:', e);
        });
    }

    private onLoadData(): void {
        this.srv.getTable().subscribe((data: any) => {
            this.rows = data;
            console.log('rows', this.rows);
        }, (e) => {
            console.log('ERROR:', e);
        });
    }

    private onLoadData2(): void {
        this.srv.getTable2().subscribe((data: any) => {
            this.rows2 = data;
            console.log('rows2', this.rows2);
        }, (e) => {
            console.log('ERROR:', e);
        });
    }


}
